            {id:nanoid(),img:TecImg3,title:'小程序定位地图模块全系列开发教学（超详细）',content:'前言：如果想最大化吸取本文经验，须有小程序开发基础，本文细节比较多（多看注释的提醒内容），请耐心理解，多动手尝试，收获会更加丰富 1.定位系统使用场景及概述如美团外卖小程序4.外部跳转当我们点击外部的位置信息，就跳转到内部的定位模块，刚刚我们在上面给外部的标签设置了触摸事件getLocation，接下来只要到js里面设置点（navigateTo就可以了，但由于我们的位置信息是用全局变量赋值的，所以我们要在app.js设置一个全局变量，代码'},
            {id:nanoid(),img:TecImg4,title:'避雷！小程序全局userInfo值为空的问题',content:'1.发现问题最近在做一个小程序的登录页面的时候，要验证用户是否已经授权过小程序获得用户信息，所以在app.js里面的onLaunch函数（小程序初始化执行）用了api里面的getSetting方法，代码如下,然后在globalData里面初始化userInfo,好了接下来我们到登录页面去写一个授权的点击按钮,2.问题关键 好的此时小程序的数据里面已经保存了我们同意授权的信息，我们再次编译页面来获取userInfo的值并把它保存到全局变量中（app.js的onLaunch中操作），并在login.js中输出它，需要格外注意的是我们此时编译的页面是login.wxml'}
